:root {
  --font-poppins: "Poppins", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

:root {
  --tomato: hsl(360, 100%, 69%);
  --client: hsl(26, 96%, 56%);
  --graduation: hsl(34, 100%, 65%);
  --blue: hsl(213, 54%, 21%);
  --citizen: hsl(195, 100%, 65%);
  --promo: hsl(264, 100%, 50%);
  --brainstorm: hsl(250, 100%, 65%);
  --white: hsl(0, 0%, 100%);
  --copyright: hsl(0, 0%, 79%);
  --light-text: hsl(0, 0%,55%);
  --border: hsl(0, 0%, 44%);
  --dark-text: hsl(0, 0%,20%);
  --comment: hsl(0, 0%, 13%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-size: 0.625rem;
}
@media (min-width: 71.875em) {
  html {
    font-size: 1rem;
  }
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  color: var(--white);
  font-family: var(--font-open-sans);
}
body.noscroll {
  overflow: hidden;
}

h1,
h2,
h3,
h4 {
  margin-top: 0;
  line-height: 1.1;
}

h2,
h4 {
  color: var(--dark-text);
}

h1 {
  font-size: 3.125rem;
  font-weight: 500;
  margin-bottom: 1.125rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 4.0625rem;
  }
}

h2 {
  font-size: 2.25rem;
  font-weight: 400;
  margin-bottom: 2rem;
}

h3 {
  text-align: left;
  position: relative;
  font-size: 1.875rem;
  font-weight: 400;
  padding-right: 3.125rem;
  padding-bottom: 0.625rem;
  margin-bottom: 1.5625rem;
}
h3::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 8.4375rem;
  width: 4.75rem;
  height: 0.0625rem;
  background-color: var(--white);
}

h4 {
  font-size: 1.5625rem;
  font-weight: 400;
  margin-bottom: 1rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: var(--white);
  font-size: 1.125rem;
}

p {
  margin: 0;
}

input {
  padding: 1.125rem 4.0625rem 1.125rem 1.875rem;
  border-radius: 2.8125rem;
  font-size: 1rem;
  border: 0;
}

.container {
  padding: 0 1.625rem;
  max-width: 70.125rem;
  margin: 0 auto;
}

.promo {
  background: var(--promo) url("../images/background.png") no-repeat 50% -150%/150vh;
  height: 56.25rem;
}
@media (min-width: 71.875em) {
  .promo {
    background-position: 50% 100%;
    background-size: 250vh;
    height: 100vh;
  }
}

.swiper {
  cursor: grab;
  padding-bottom: 0.9375rem;
}
.swiper-wrapper {
  max-width: 29.5rem;
  text-align: center;
}
.swiper-pagination-bullet {
  width: 0.6875rem;
  height: 0.6875rem;
  background-color: var(--client);
}

button, .button {
  color: var(--white);
  background-color: var(--tomato);
  border-bottom: 0.0625rem solid var(--border);
  padding: 0.5625rem 2rem 0.75rem;
  border-radius: 1.375rem;
  border-width: 0;
}

.button__menu {
  font-size: 1.125rem;
  cursor: pointer;
  display: none;
}
@media (min-width: 71.875em) {
  .button__menu {
    display: block;
  }
}
.button__input {
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 0;
  background-color: var(--blue);
  padding-top: 20%;
  padding-right: 10%;
}

.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1.5625rem;
  padding-bottom: 1.5625rem;
}
@media (min-width: 71.875em) {
  .header {
    padding-top: 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .header__toggle {
    display: none;
  }
}
.header__toggle > span {
  display: block;
  width: 1.625rem;
  height: 0.25rem;
  border-radius: 1.40625rem;
  background-color: var(--white);
  transition: all 200ms ease-in-out;
  transform-origin: 0.046875rem 0.109375rem;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 0.3125rem;
}
.header.open .header__toggle > span:first-child {
  transform: rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  opacity: 0;
}
.header.open .header__toggle > span:last-child {
  transform: rotate(-45deg);
}
.header__menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background: var(--white);
  margin-top: 0.09375rem;
  padding: 1.625rem;
  border-radius: 1.40625rem;
  z-index: 1;
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: var(--dark-text);
}
.header__menu button {
  display: block;
  margin: 0.625rem;
  text-align: center;
}
.header__links {
  display: none;
}
@media (min-width: 71.875em) {
  .header__links {
    display: block;
  }
}
.header__links a {
  font-size: 1.125rem;
}
.header__links a:not(:last-child) {
  margin-right: 3.875rem;
}

.hero {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  text-align: center;
  margin-top: 0;
}
@media (min-width: 71.875em) {
  .hero {
    flex-direction: row;
    text-align: left;
    margin-top: 3.75rem;
  }
}
.hero__text {
  margin-right: 0;
  font-family: var(--font-open-sans);
}
@media (min-width: 71.875em) {
  .hero__text {
    margin-right: 2.5rem;
  }
}
.hero__subtitle {
  font-size: 1.0625rem;
  line-height: 1.875rem;
  margin-bottom: 3.125rem;
}
.hero img {
  width: 50%;
  margin-left: 0;
  margin: 3.125rem 0 6.25rem;
}
@media (min-width: 71.875em) {
  .hero img {
    margin: 0;
  }
}

.admission {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12.5rem;
}
@media (min-width: 71.875em) {
  .admission {
    flex-direction: row;
  }
}
.admission__subscribe {
  position: relative;
  top: 3.75rem;
  margin-bottom: 6.25rem;
  background-color: var(--tomato);
  box-shadow: 0 -0.625rem 0 var(--blue);
  font-family: var(--font-poppins);
  padding: 3.28125rem 2.8125rem;
  border-radius: 0.625rem;
  width: 28.125rem;
  text-align: center;
}
@media (min-width: 71.875em) {
  .admission__subscribe {
    top: -5.625rem;
    margin-bottom: 0;
  }
}
.admission__subtitle {
  font-size: 1.25rem;
  margin-bottom: 0.9375rem;
  opacity: 0.5;
}
.admission__form {
  position: relative;
  max-width: 15rem;
  border-radius: 2.8125rem;
  overflow: hidden;
  margin: 0 auto;
}
.admission__text {
  color: var(--comment);
  line-height: 1.875rem;
  margin-bottom: 1.25rem;
}
.admission__client {
  color: var(--client);
  margin-bottom: 2.5625rem;
}

.latest {
  margin-bottom: 12.5rem;
}
.latest__content {
  display: flex;
  flex-direction: column;
  text-align: center;
}
@media (min-width: 71.875em) {
  .latest__content {
    flex-direction: row;
    text-align: left;
  }
}
.latest__content img {
  width: 50%;
  margin: 0 auto 2.6875rem;
}
@media (min-width: 71.875em) {
  .latest__content img {
    margin: 0;
  }
}
.latest__content:first-child {
  margin-bottom: 6.25rem;
}
@media (min-width: 71.875em) {
  .latest__content:last-child {
    flex-direction: row-reverse;
  }
}
.latest__subtitle {
  font-size: 1.0625rem;
  margin-bottom: 2.1875rem;
  color: var(--light-text);
}
.latest__subtitle:nth-child(3) {
  margin-bottom: 4.6875rem;
}

.middle {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
@media (min-width: 71.875em) {
  .middle {
    flex-direction: row;
  }
}
.middle__content {
  max-width: 21.25rem;
  margin-bottom: 6.25rem;
}
.middle__content .middle__image {
  position: relative;
  width: 8.4375rem;
  height: 8.4375rem;
  margin: 0 auto;
  margin-bottom: 2.6875rem;
  border-radius: 50%;
}
.middle__content .middle__image::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 4.5rem;
  height: 4.5rem;
}
.middle__content:nth-child(1) .middle__image {
  box-shadow: 0 0 1.25rem var(--citizen);
  background-color: var(--citizen);
}
.middle__content:nth-child(1) .middle__image::after {
  background: url("../images/global-citizen.svg") no-repeat 50% 50%/contain;
}
.middle__content:nth-child(2) .middle__image {
  box-shadow: 0 0 2.5rem var(--brainstorm);
  background-color: var(--brainstorm);
}
.middle__content:nth-child(2) .middle__image::after {
  background: url("../images/brainstorm-skill.svg") no-repeat 50% 50%/contain;
}
.middle__content:nth-child(3) .middle__image {
  box-shadow: 0 0 2.5rem var(--graduation);
  background-color: var(--graduation);
}
.middle__content:nth-child(3) .middle__image::after {
  background: url("../images/graduation-cap.svg") no-repeat 50% 50%/contain;
}
.middle__subtitle {
  margin-top: 1.0625rem;
  line-height: 1.6875rem;
  color: var(--light-text);
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6.25rem;
  font-family: var(--font-poppins);
}
@media (min-width: 71.875em) {
  .footer {
    flex-direction: row;
  }
}
.footer, .footer a {
  color: var(--copyright);
  font-size: 0.875rem;
}
.footer__copyright {
  margin-bottom: 2.5rem;
}
@media (min-width: 71.875em) {
  .footer__copyright {
    margin: 0;
  }
}
.footer__menu a {
  display: block;
  text-align: center;
  margin-bottom: 0.625rem;
}
@media (min-width: 71.875em) {
  .footer__menu a {
    display: inline-block;
  }
}
@media (min-width: 71.875em) {
  .footer__menu a:not(:last-child) {
    margin-right: 2.5rem;
  }
}